import { useEffect, useState } from "react";
import React from "react";
import logo from "./logo.svg";
import { LocalizationProvider } from "@mui/x-date-pickers";
import { AdapterDayjs } from "@mui/x-date-pickers/AdapterDayjs";
import { v4 as uuidv4 } from "uuid";
import Form from "./components/Form";
import List from "./components/List";
import "./App.css";
import axios from "axios";
import "bootstrap/dist/css/bootstrap.css";
interface Item {
  uuid: string;
  name: string;
  produce: string;
  expire: number;
}

function request(items: Item[]) {
  axios
    .put("https://www.rnadint.com/items", items)
    .then(() => console.log("success."))
    .catch((error) => console.log(error));
}

function App() {
  const [items, setItems] = useState<Item[]>([]);
  useEffect(() => {
    axios
      .get<Item[]>("https://www.rnadint.com/items", {
        headers: {
          "Access-Control-Allow-Origin": "*",
          "Content-Type": "application/json",
          "ngrok-skip-browser-warning": "true",
        },
      })
      .then((res) => setItems(res.data));
  }, []);

  return (
    <LocalizationProvider dateAdapter={AdapterDayjs}>
      <div className="mb-5">
        <Form
          onSubmit={(itemForm) => {
            setItems([...items, { ...itemForm, uuid: uuidv4() }]);
            request([...items, { ...itemForm, uuid: uuidv4() }]);
          }}
        />

        <List
          items={items}
          onDelete={(uuid) => {
            setItems(items.filter((e) => e.uuid !== uuid));
            request(items.filter((e) => e.uuid !== uuid));
          }}
        />
        <button
          className="btn btn-secondary"
          onClick={() => window.location.reload()}
        >
          Refresh
        </button>
      </div>
    </LocalizationProvider>
  );
}

export default App;
